import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { DefaultSubscriptionsGuard } from './default-subscriptions.guard';

@Injectable()
export class DefaultSubscriptionsNavigationFactory implements NavigatorNodeFactory {
  private navNode = new NavigatorNode({
    label: gettext('Default subscriptions'),
    path: '/default-subscriptions',
    icon: 'c8y-c8y-data',
    parent: gettext('Applications'),
    priority: 1000
  });

  constructor(private defaultSubscriptionsGuard: DefaultSubscriptionsGuard) {}

  /** Returns the navigation node if the feature is active. */
  async get(): Promise<NavigatorNode> {
    if (await this.defaultSubscriptionsGuard.canActivate()) {
      return this.navNode;
    }
  }
}
